.loginFormBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    height: 640px; 
    max-width: 100%;
    border-radius: 13px;
    background-color: rgb(255, 255, 255);
    box-shadow: 1px 2px 10px 2px #7f5d9c;
}
.loginFormBlock .rememberMeBlock{
    display: flex;  
    width: 250px;
    padding: 3px;;
    align-items: center;
    
}
.loginFormBlock button{
   width: 250px;
}
.loginAccount{
    font-weight: 500;
    padding-top: 30px;
}
.loginAccountData{
    font-weight: 400;
}
.loginAccountData div{
    padding: 10px;
}

@media (max-width: 560px) {
    .loginFormBlock{
        border-radius: 0;
        height: 100vh;
    }
}
  